.conversation{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
margin-top: 20px;
}
.conversation:hover{
    background-color: rgb(236, 234, 234);
}
.conversationimg{
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}
.conversationname{
    font-weight: 500;
}